@import url('https://fonts.googleapis.com/css2?family=Outfit:wght@100;200;300;400;500;600;700&family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

.weathercontainer body{
    font-family: 'Outfit', 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.weathercontainer {
  width: 100%;
  height: 100vh;
  position: relative;
  background-color: rgba(0,0,0,0.4);
  color: #fff;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.weathercontainer:before {
  content: '';
  background: url('../assets/sunset.jpg') no-repeat center center/cover;
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: -1;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
.weathercontainer p{
    font-size: 1.6rem;
}
  
.weathercontainer h1 {
    font-size: 6rem;
}

.weathercontainer .search {
  text-align: center;
  
  
}

.weathercontainer input {
  padding: .7rem 1.5rem;
  font-size: 1.2rem;
  border-radius: 25px;
  border: 1px solid rgba(255,255,255, 0.8);
  background: rgba(255,255,255, 0.1);
  color: #f8f8f8;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.weathercontainer ::placeholder {
    padding-top: 10px;
    color: #f8f8f8;
}

.weathercontainer1 {
    max-width: 700px;
    height: 700px;
    margin: auto;
    padding: 0 1rem;
    position: relative;
    top: 10%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.weathercontainer .weathertop {
    width: 100%;
    margin: 1rem auto;
  }
  
  .weathercontainer .weatherdescription {
    position: relative;
    right: -90%;
    transform-origin: 0 0;
    transform: rotate(269deg);
  }
  
.weathercontainer .weatherbottom {
    display: flex;
    justify-content: space-evenly;
    text-align: center;
    width: 100%;
    padding: 1rem;
    border-radius: 12px;
    background-color: rgba(255,255,255, 0.2);
    margin-bottom: 100px;
}

.weatherbold {
    font-weight: 700;
  }

